/**
 * 
 */
export const getCurDaysOfMonth = () => {
  const curDate = new Date();
  const curMonth = curDate.getMonth();
  const curTime = curDate.getTime();
  const nextTime = curDate.setMonth(curMonth + 1);
  return Math.ceil((nextTime - curTime) / (24 * 60 * 60 * 1000));
};
/**
 * 
 */
export const getFirstDayWeek = () => {
  const curDate = new Date();
  curDate.setDate(1);
  return curDate.getDay();
};

/**
 * 
 */
export const formatDate = () => {
  const date = new Date();
  return {
    year: date.getFullYear(), // 
    month: date.getMonth() + 1, // 
    day: date.getDate(), // 
    week: date.getDay(), // 
    curDays: getCurDaysOfMonth(),
    firstDayWeek: getFirstDayWeek(),
  };
};
